import './App.css';
import { useMemo, useEffect, useState } from 'react';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import Component from './Component';
import { useDrop } from 'react-dnd'
import { ItemTypes } from './Constants'
import { Module } from './Module'

function App() {
  const module = useMemo(() => new Module(), []);
  const pxpmm = parseInt(window.getComputedStyle(document.getElementById("header"), null).height)/20;
  return (
    <DndProvider backend={HTML5Backend}>
      <Panel module={module} pxpmm={pxpmm}></Panel>
    </DndProvider>
  );
}

function Panel(props) {
  const [components, setComponents] = useState(props.module.components.map(c => ({...c})));
  useEffect(() => {
    function cloneComponents(components){
      setComponents(components.map(c => ({...c})));
    }
    const unobserve = props.module.observe(cloneComponents);
    return unobserve;
  });
  const [, drop] = useDrop(
  () => ({
    accept: ItemTypes.COMPONENT,
    drop: (item, monitor) => {
      const {x, y} = monitor.getDifferenceFromInitialOffset();
      props.module.moveComponent(
        item.index,
        (x / props.pxpmm),
        (y / props.pxpmm));
    }
  }),
  []
)
const componentElems = components.map((c, i) => <Component key={i} index={i} component={c} />);
  return (
    <div id='panel_container'>
      <div id='panel' className='hp4'>
        <div id='shadow'>
        </div>
        <div  ref={drop} id='front'>
          <div id='topscrew' className='screw'>
          </div>
          <div id='face'>
            {componentElems}
          </div>
          <div id='bottomscrew' className='screw'>
          </div>
        </div>
      </div>
    </div>
  );
};

export default App;
