export class Module{
  constructor(){
    this.components = [
      {image:'SCHURTER_SMS_PG09_A', x:5, y:20},
      {image:'NKK_SERIES JF_ROUND_ROUND_GREY', x:5, y:50}
    ]
    this.observers = []
  }
  observe(o){
    this.observers.push(o)
    return () => {
      this.observers = this.observers.filter((t) => t !== o)
    }
  }
  moveComponent(index, offsetX, offsetY) {
    this.components[index].x += offsetX;
    this.components[index].y += offsetY;
    this.emitChange(index)
  }
  emitChange(index) {
    const coms = this.components
    this.observers.forEach((o) => o && o(coms))
  }
}
